export default {
		name: "uni-countdown",
		
		data() {
			return {
				setTime: null,
				h: '00',
				i: '00',
				s: '',
				leftTime: 0,
				text:'获取验证码',
				id:1
			}
		},
		
		methods: {
			onInput(e){
				return RegExp(/^1[34578]\d{11}$/).test(e)
				alert(e);
				//不用v-model绑定表单，直接时间获取值 这是uni-app官方方式
				console.log(e.detail)//{value:"12345678910",cursor:1}
				this.phone = e.detail
			},
			setInterValFunc: function(obj) {
				this.setTime = setInterval(function() {
					obj.countDown(obj);
				}, 1000);
			},
			click:function(){
				this.text = '重新获取'
				this.s = 60
			},
			countDown: function(self) {
				if(self.text == '重新获取'){
					if(self.s > 0){
						self.s -= 1;
					}else if(self.s == 0){
						self.s = 60;
						self.text = '获取验证码'
					}
				}
			},
		    tijiao(e){
		    	console.log(e);//接收用户输入的values
		    	uni.setStorageSync("phone","phone");
		        debugger ;//调节器
				var phone = e.detail.value;
				console.log(e.detail.value);
			    //当注册成功跳转登录页面，，跳出一个弹框显示
			    uni.showModal({
			    	title: '友情提示',
			    	content: '注册成功！请往登录界面',
			    	success:function(res){
			    		// alert(res);
						//点击确定后跳转界面
						if(res.confirm){
							uni.switchTab({
								url:'../login/login'
							})
						}
			    	}
			    });		
		    },
		},
	}
